<template>
  <div class="count-box">
    <button class="minus" @click="subHandle">-</button>
    <input type="text" class="inp" :value="value" @change="inpHandle">
    <button class="add" @click="addHandle">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    subHandle () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    addHandle () {
      this.$emit('input', this.value + 1)
    },
    inpHandle (e) {
      const num = +e.target.value
      if (isNaN(num) || num <= -1) {
        e.target.value = this.value
        return
      }

      this.$emit('input', this.value)
    }
  }
}
</script>

<style lang="less" scoped>
  .count-box{
    display: flex;
    width: 110px;

    .minus, .add {
      width: 30px;
      height: 30px;
      outline: none;
      border: none;
      background-color: #efefef;
    }

    .inp {
      width: 40px;
      height: 30px;
      margin: 0 5px;
      outline: none;
      border: none;
      background-color: #efefef;
      text-align: center;
    }
  }
</style>
